import React from 'react';
import Clock from './components/Clock';

import './App.scss';

class App extends React.Component {
  state = {
    isClockVisible: true
  }

  render() {
    return (
    <div className="App">
      <h1>React clock</h1>
      {this.state.isClockVisible && <Clock isClockVisible={ this.state.isClockVisible }/>}
      <button type='button' onClick={
        () => {
          this.setState({
            isClockVisible: false
          })
        }
      }>
        hide clock
      </button>
      <button type='button' onClick={
        () => {
          this.setState({
            isClockVisible: true
          })
        }
      }>
        show clock
      </button>
    </div>
  );
  }
}

export default App;
