import React from 'react';

class Clock extends React.Component {
  state = {
    go: true,
  }

  componentDidMount() {
    this.goClock()
  }

  componentDidUpdate(prevProps, prevState) {
    if(this.props.isClockVisible !== false) {
      this.goClock()
    }
  }

  componentWillUnmount() {
    clearTimeout(this.goClock);
  }

  goClock = () => {
    setTimeout(()=>this.setState({ go: !this.state.go }), 1000);
  }

  render () {
    const date = new Date();
    console.log(date.toLocaleTimeString());

    return (
      <p>
        Current time:
        {' '}
        {date.toLocaleTimeString()}
      </p>
    )
  }
}

export default Clock;
